import React from "react";
import Typography from "@material-ui/core/Typography";
import dayjs from "dayjs";

export default function DateComponent({ startTime, endTime }) {
  const formatted = dayjs(startTime).format("YYYY. M. D.");
  const start = dayjs(startTime).format("H M");
  const end = dayjs(endTime).format("H M");
  return (
    <div>
      <Typography variant="h6">{formatted}</Typography>
    </div>
  );
}
