import deciduousTree from "@iconify-icons/twemoji/deciduous-tree"; // 
import dogIcon from "@iconify-icons/twemoji/dog"; // 
import dollarBanknote from "@iconify-icons/twemoji/dollar-banknote"; // 
import droolingFace from "@iconify-icons/twemoji/drooling-face"; //    
import faceWithSymbolsOnMouth from "@iconify-icons/twemoji/face-with-symbols-on-mouth"; // 
import faceWithThermometer from "@iconify-icons/twemoji/face-with-thermometer"; //  
import firstQuarterMoonFace from "@iconify-icons/twemoji/first-quarter-moon-face"; // 
import grinningFace from "@iconify-icons/twemoji/grinning-face"; // 
import hamburgerIcon from "@iconify-icons/twemoji/hamburger"; // 
import hotBeverage from "@iconify-icons/twemoji/hot-beverage"; //  ()
import redHeart from "@iconify-icons/twemoji/red-heart"; // 
import rollingOnTheFloorLaughing from "@iconify-icons/twemoji/rolling-on-the-floor-laughing"; //  
import shortcakeIcon from "@iconify-icons/twemoji/shortcake"; // 
import sleepingFace from "@iconify-icons/twemoji/sleeping-face"; // 
