// 





<Icon icon={cloudIcon} />; //    

export function getIconComponent(key)
{
  return <Icon icon={getIcon(key)} width="2em"/> 
}


export function getIcon(key)
{
  switch (key)
  {
    case "sad":
    case "":
    case "":
      return cryingFace

    case "angry":
    case "":
      return faceWithSymbolsOnMouth
    
    case "excited":
    case "happy":
    case "":
    case " ":
      return rollingOnTheFloorLaughing

    case "tired":
    case "exhausted":
    case "":
    case "sleepy":
      return sleepingFace

    case "":
    case "delicious":
    case "yummy":
      return droolingFace

    case "":
    case "fever":
    case "sick":
    case "":
      return faceWithThermometer

    case "sunny":
      return sunIcon
    case "cloudy":
      return cloudIcon
    case "rainy":
      return cloudWithRain

    case "dog":
      return dogIcon

    default:
    return catIcon
  }
}

export const hashtagsDB = [
    "happy", "sick", "dog", "cat", "angry", 
    "excited", "tired", "exhausted", "sleepy", 
    "delicious", "yummy", "sunny", "cloudy", "rainy"
]